const cardDescriptions = document.querySelectorAll(".card__description");
let cardDescriptionsHidden = document.querySelectorAll(".card__description-hidden");
const showMoreButtons = document.querySelectorAll(".card__show-more");
const navbar = document.querySelector(".navbar__container");
const illustration = document.querySelector(".hero__illustration-container");
const heroText = document.querySelector(".hero__text-container");
const projectCards = document.querySelectorAll(".card");
const showCardsButton = document.querySelector(".show-cards-button");
const buttonViewProjects = document.querySelector('[data-js="scroll-to-projects"]');
const navbarProjectsLink = document.querySelector('[data-js="nav-projects"]');
const navbarContactLink = document.querySelector('[data-js="nav-contact"]');

//scroll animation library
let scrollOut = ScrollOut({
    threshold: .3,
    once: true
});

navbarProjectsLink.addEventListener("click", () => {
    window.scrollTo({
        top: 600,
        left: 0,
        behavior: 'smooth'
    });
})

navbarContactLink.addEventListener("click", () => {
    window.scrollTo({
        top: document.body.scrollHeight - 400,
        left: 0,
        behavior: 'smooth'
    });
})

buttonViewProjects.addEventListener("click", () => {
    window.scrollTo({
        top: 600,
        left: 0,
        behavior: 'smooth'
    });
})

showMoreButtons.forEach((btn, i) => {
    btn.addEventListener("click", (e) => {
        if (btn.textContent === "Read More") {
            //set height of description to the expanded description height
            cardDescriptions[i].style.height = (cardDescriptionsHidden[i].clientHeight + 20) + "px";
            //user expanded description, so dont hide it when new element descriptions are collapsed
            cardDescriptions[i].isShowing = true;
            btn.textContent = "Show Less";
        } else {
            cardDescriptions[i].style.height = "1.5em";
            btn.textContent = "Read More";
        }

    })
})

//parallax effect on illustration and navbar change on scroll
window.addEventListener("scroll", () => {
    illustration.style.marginTop = (320 - window.pageYOffset / 3).toString() + "px";

    if (window.pageYOffset > 100 && window.pageYOffset <= 560) {
        illustration.style.visibility = "visible"
        if (!navbar.classList.contains("is-scrolling")) { navbar.classList += " is-scrolling"; }
    }
    if (window.pageYOffset <= 100) {
        navbar.classList.remove("is-scrolling");
    }
})

//---show more button code--
//if there are more than 3 cards, show more button appears
if (projectCards.length > 3) {
    showCardsButton.style.display = "inline-block";
    projectCards.forEach((card, i) => {
        if (i > 3) {
            projectCards[i].style.display = "none";
        }
    })
} else {
    showCardsButton.style.display = "none";
}
showCardsButton.addEventListener("click", () => {
    projectCards.forEach((card, i) => {
        if (i > 2) {
            projectCards[i].style.display = "grid";
        }
        showCardsButton.style.display = "none";
    })
    //update scrollout to animate newly shown cards
    scrollOut.update();
})

//make svg mimic object-fit cover on small screens
let projVector = document.querySelector(".projects__vector");

window.onload = () => {
    projVector.setAttribute("height", "100%");
    if (window.innerWidth < 900) {
        projVector.setAttribute("preserveAspectRatio", "xMinYMid slice");
    }
}

window.onresize = () => {
    if (window.innerWidth < 900) {
        projVector.setAttribute("preserveAspectRatio", "xMinYMid slice");
    } else {
        projVector.setAttribute("preserveAspectRatio", "none");
    }
}
